.feedback {
    padding: 61px 0 48px;
    background-color: #f4f5f8;

    &__wrap {
        display: flex;
        align-items: flex-start;
    }

    .content {
        width: calc(100% - 330px);
        padding-right: 70px;
    }

    .title-row {
        margin-bottom: 23px;
    }
    .tabs {
        margin-left: 49px;

        &__item {
            font-size: 17px;
            padding: 10px 16px;
            margin: 6px 6px 0 0;
        }
    }

    .sidebar {
        background-color: @blue;
        width: 330px;
        margin-top: -20px;
        padding: 32px 25px 43px;

        &__title {
            font-size: 25px;
            font-weight: 600;
            color: @white;
            margin-top: 0;
            margin-bottom: 30px;
        }

        &__item {
            display: flex;

            &:not(:last-of-type) {
                margin-bottom: 20px;
            }

            .img {
                min-width: 50px;
                height: 50px;
                border-radius: 100px;
                overflow: hidden;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            .content {
                color: @white;
                width: auto;
                padding: 7px 0 0 15px;

                .name {
                    font-size: 17px;
                    font-weight: 600;
                    line-height: 1.1;
                    margin-bottom: 12px;
                }

                .profession,
                .address,
                .phone,
                .email {
                    font-size: 13px;
                    font-weight: 600;
                    line-height: 1.2;
                    margin-bottom: 12px;
                }

                .email {
                    margin-bottom: 0;
                }
            }
        }
    }
}

.form {

    &__item {
        display: block;
        margin-bottom: 25px;

        

        &-name {
            margin: 0 0 8px;
            font-size: 15px;
            font-weight: 600;
            color: @blue;
        }

        &-input {
            width: 100%;
            height: 39px;
            background-color: @white;
            border: 1px solid #cacfdd;
            outline: none;
            padding: 10px;
            font-size: 15px;
            color: @blue;
            font-family: @PFBeau;

            &.error {
                border-color: red;
            }
        }

        &-comment {
            width: 100%;
            height: 119px;
            background-color: @white;
            border: 1px solid #cacfdd;
            outline: none;
            padding: 10px;
            font-size: 15px;
            color: @blue;
            font-family: @PFBeau;

            &.error {
                border-color: red;
            }
        }
    }

    &__row {
        display: flex;
        margin: 0 -10px;

        .form__item {
            width: calc(50% - 20px);
            margin-left: 10px;
            margin-right: 10px;
        }
    }

    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__agree {
        position: relative;
        cursor: pointer;

        .agree {
            position: absolute;
            visibility: hidden;
            opacity: 0;

            &.error + .form__agree-text {
                &:before {
                    border-color: red;
                }
                
            }

            &:checked + .form__agree-text {
                &:after {
                    opacity: 1;
                }
            }
        }

        &-text {
            font-size: 13px;
            font-style: italic;
            position: relative;
            padding-left: 43px;

            &:before {
                content: '';
                position: absolute;
                left: 0;
                top: 0;
                bottom: 0;
                margin: auto;
                width: 30px;
                height: 30px;
                border: 1px solid #cacfdd;
            }

            &:after {
                content: '';
                position: absolute;
                left: 9px;
                top: 0;
                bottom: 0;
                margin: auto;
                width: 13px;
                height: 10px;
                background-image: url("../img/icons/agree.png");
                background-size: 13px 10px;
                opacity: 0;
                transition: 0.3s;
            }

            a {
                text-decoration: underline;
            }
        }
    }

    &__submit {
        background-color: @blue;
        color: @white;
        font-family: @PFBeau;
        font-size: 17px;
        font-weight: 600;
        border-radius: 100px;
        border: 1px solid @blue;
        padding: 10px 15px;
        transition: 0.3s;

        &:hover {
            color: @blue;
            background-color: transparent;
            border-color: @blue;
        }
    }
    
}

@media screen and (max-width: 1200px) {
    .feedback {
        padding: 50px 0;
    }

    .form {
        &__item {
            &-name {
                font-size: 17px;
            }
        }
    }
}

@media screen and (max-width: 992px) {
    .form {
        &__wrap {
            column-gap: 30px;
        }
    }
}

@media screen and (max-width: 576px) {
    .form {
        &__wrap {
            grid-template-columns: 1fr;
            column-gap: 0;
            grid-template-areas:"firstname"  
                                "phone"
                                "comment";
        }

        &__footer {
            flex-direction: column;
            align-items: flex-start;
        }

        &__submit {
            margin-top: 30px;
        }
    }
}